<!--
    This component is used inside the dashboard component.
    It serves to list all the contracts that the user has
-->

<template>

    <v-card>
        <v-card-title>My Contracts</v-card-title>
        <v-card-text>
            <v-data-table
                :headers="headers"
                :items="userContracts"
                dense
                hide-default-footer
                :loading="!userContracts"
                loading-text="Loading... Please wait"
                @click:row="rowClicked"
                class="row-pointer"
            >

                <template v-slot:body="props">
                    <tbody name="table-rows" is="transition-group">
                        <tr v-for="(item, index) in props.items" :key="index">
                            <td>
                                <v-tooltip v-if="item.verified" color="info" bottom>
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-icon v-bind="attrs" v-on="on" color="primary">mdi-check-circle-outline</v-icon>
                                    </template>
                                    <span>Contract verified</span>
                                </v-tooltip>

                                <v-tooltip v-else color="info" bottom>
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-icon v-bind="attrs" v-on="on" color="error">mdi-alpha-x-circle-outline</v-icon>
                                    </template>
                                    <span>Contract unverified</span>
                                </v-tooltip>

                                {{ item.name }}
                            </td>
                            <td>{{item.abi}}</td>
                            <td>{{item.chainName}}</td>
                        </tr>
                    </tbody>
                </template>

            </v-data-table>
        </v-card-text>
    </v-card>

</template>

<script>

    import { mapState, mapActions } from 'vuex';
    import { endpoints } from '@/store/constants';

    export default {
        name: 'OwnedContracts',

        data() {
            return {
                chains: [],
                search: '',
                headers: [
                    { text: 'Name', value: 'name' },
                    { text: 'Type', value: 'abi' },
                    { text: 'Blockchain', value: 'chainName' }
                ],
            }
        },

        mounted() {
            this.chains = endpoints;

            if (!this.userContracts) {
                this.getUserContracts();
            }
        },

        computed: {
            ...mapState({
                'w3': state => state.chain.w3,
                'userContracts': function (state) {
                    const cnts = [];

                    if (state.db.userContracts) {
                        for (let i in state.db.userContracts) {
                            const cnt = state.db.userContracts[i];

                            const chn = this.chains.filter( chain => chain.id == cnt.chain)[0];
                            if (chn) cnt.chainName = chn.name;

                            if (!this.w3.utils.isAddress(cnt.name)) {
                                cnts.push(cnt);
                            }
                        }
                    }

                    return cnts;
                }
            })
        },

        methods: {
            ...mapActions({
                getUserContracts: 'db/getUserContracts'
            }),

            rowClicked(e) {
                this.$router.push({ name: 'Manager', query: { address: e.address } });
            }
        }
    }

</script>

<style scoped>

    .v-icon {
        vertical-align: top;
        margin-right: 2px;
    }

    .v-card {
        border-radius: 10px;
    }

    .row-pointer >>> tbody tr :hover {
        cursor: pointer;
    }

    .table-rows-enter-active,
    .table-rows-leave-active {
        transition: all 0.5s ease;
    }
    
    .table-rows-enter,
    .table-rows-leave-to {
        opacity: 0;
        transform: translateY(-30px);
    }

</style>
